import Header from './Header'
import About from '../pages/about'

const layoutStyle = {
    margin: 20,
    padding: 20,
    border: '1px solid #DDD'
}

const Layout = props => {
    return (
        <div style={layoutStyle}>
            <Header />
            {props.children}
            <About />
        </div>
    )
}

export default Layout;